import ResponsiveBootstrapToolkit from '@/framework/bootstrap-toolkit.min';
import { PortalBaseViewController } from '@/views/portal/PortalBaseViewController';
import { TTAPI } from '@/services/TimeTrexClientAPI';
import '@/global/widgets/filebrowser/TImageBrowser';
import { ServiceCaller } from '@/services/ServiceCaller';
import { TTUUID } from '@/global/TTUUID';

import tinyMCE from 'tinymce/tinymce';
import 'tinymce/icons/default'; // Default icons are required for TinyMCE 5.3 or above
import 'tinymce/themes/silver/theme';
import 'tinymce/plugins/link';
import 'tinymce/plugins/autoresize';

export class PortalJobVacancyDetailController extends PortalBaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			edit_view_error_ui_dic: {},
			events: {
				'click #applyBtn': 'onApplyJobClick',
				'click #available_jobs': 'availableJobsClick',
				'click #applied_jobs': 'appliedJobsClick',
				'click #applied_btn': 'appliedJobsClick',
				'click .sign-in': 'signIN'
			}
		} );

		super( options );
	}

	init() {
		this.viewId = 'PortalJobVacancyDetail';
		this.script_name = 'JobVacancyDetailView';
		this.job_applicant_api = TTAPI.APIJobApplicantPortal;
		this.viewport = $.extend( {}, ResponsiveBootstrapToolkit );
		this.render();
	}

	render() {
		var $this = this;
		if ( LocalCacheData.getPortalLoginUser() ) {
			this.setRegisterContainerStatus( function( applied ) {
				$this.showJobVacancy( { applied: applied } );
			} );
		} else {
			this.showJobVacancy();
		}
	}

	showJobVacancy( options ) {
		var $this = this;
		var row = Global.loadWidget( 'views/portal/hr/recruitment/PortalJobVacancyDetail.html' );
		var user_data = { first_name: '', last_name: '', email: '', applied: false };
		if ( LocalCacheData.getPortalLoginUser() ) {
			user_data = {
				first_name: LocalCacheData.getPortalLoginUser().first_name,
				last_name: LocalCacheData.getPortalLoginUser().last_name,
				email: LocalCacheData.getPortalLoginUser().email
			};
		}
		if ( Global.isSet( options ) && Global.isSet( options.applied ) ) {
			user_data.applied = options.applied;
		}
		for ( var key in this.model.attributes ) {
			if ( this.model.get( key ) == false ) {
				this.model.set( key, '' );
			}
		}
		this.$el.html( _.template( row )( $.extend( user_data, this.model.toJSON() ) ) );
		if ( !_.isUndefined( $( '.tinymce-textarea' )[0] ) ) {
			var calc_tinymce_height = setInterval( function() {
				var iframe_height = $( '.tinymce-textarea' ).find( 'iframe' ).height();
				var body_height = $( '.tinymce-textarea' ).find( 'iframe' ).contents().find( 'body' ).height();
				if ( _.isUndefined( $( '.job-detail-summary-description' )[0] ) ) {
					$( '.tinymce-textarea' ).find( 'iframe' ).contents().find( 'body' ).css( {
						'background-color': '#F6F6F6',
						'font-size': '14px',
						'margin-top': 0,
						'color': '#404042'
					} );
					$( '.tinymce-textarea' ).find( 'iframe' ).contents().find( 'body p:first-child' ).css( 'margin-top', 0 );
				} else {
					$( '.tinymce-textarea' ).find( 'iframe' ).contents().find( 'body' ).css( {
						'margin': 0,
						'background-color': '#E8E8E8',
						'font-size': '14px',
						'margin-top': 0,
						'color': '#404042'
					} );
					$( '.tinymce-textarea' ).find( 'iframe' ).contents().find( 'body p:first-child' ).css( 'margin-top', 0 );
				}
				if ( iframe_height > body_height ) {
					clearInterval( calc_tinymce_height );
					$( '.tinymce-textarea' ).find( 'iframe' ).css( 'height', body_height );
				}
			}, 50 );
		}
		this.$( 'input[type="text"], input[type="email"]' ).each( function() {
			$( this ).unbind( 'focus' ).bind( 'focus', function() {
				$( this ).parent().addClass( 'job-apply-form-focus' );
			} );
		} );
		this.$( 'input[type="text"], input[type="email"]' ).each( function() {
			$( this ).unbind( 'blur' ).bind( 'blur', function() {
				$( this ).parent().removeClass( 'job-apply-form-focus' );
			} );
		} );

		this.setDetailAndApplyFormHeight();
		$( window ).resize(
			$this.viewport.changed( function() {
				$this.setDetailAndApplyFormHeight();
			} )
		);
		// if ( LocalCacheData.getPortalLoginUser() ) {
		// 	this.setRegisterContainerStatus();
		// } else {
		// 	$this.$('.apply-form .form-group').show();
		// }
		this.uploader = this.$( '.register-resume' ).TImageBrowser( {
			field: 'file',
			name: 'filedata',
			accept_filter: '*',
			changeHandler: function( a ) {
				var file_name = $( this ).find( '.browser' )[0].files[0].name;
				$this.$( '.apply-form #file_name' ).val( file_name );
			}
		} );

		// start- from html PortalJobVacancyDetail.html

		// if ( $.browser.mozilla ) {
		//    tinymce.editors = [];
		// } else {
		var i, t = tinyMCE.editors;
		for ( i in t ) {
			if ( t.hasOwnProperty( i ) ) {
				t[i].remove();
			}
		}
		//                                }
		tinymce.init( {
			plugins: "autoresize, link",
			// height: '300px',
			readonly: true,
			selector: '#description',
			menubar: false,
			statusbar: false,
			toolbar: false,
			theme: 'silver',
			skin_url: '../../dist/tinymce-skins/ui/oxide',
			content_css: '../../dist/tinymce-skins/content/default/content.css',
			// menubar: "insert",
			// toolbar: "link",
			// default_link_target: "_blank"

		} );

		// end - from html
	}

	setDetailAndApplyFormHeight() {
		var detail_panel_header_height = this.$( '.job-header' ).height();
		var apply_form__header_height = this.$( '.job-apply' ).height();
		if ( this.viewport.is( 'md' ) || this.viewport.is( '>md' ) ) {
			if ( detail_panel_header_height !== apply_form__header_height ) {
				if ( detail_panel_header_height > apply_form__header_height ) {
					this.$( '.job-apply' ).height( detail_panel_header_height - 20 );
				} else {
					this.$( '.job-header' ).height( apply_form__header_height + 20 );
				}
			}
		} else if ( this.viewport.is( '<=sm' ) ) {
			this.$( '.job-apply' ).height( 70 );
		}
	}

	onApplyJobClick() {
		var $this = this;
		if ( LocalCacheData.getPortalLoginUser() ) {
			this.getJobApplication( function( result ) {
				if ( result.isValid() ) {
					var result_data = result.getResult();
					if ( !Global.isArray( result_data ) ) {
						result_data = [];
					}
					if ( result_data.length > 0 ) {
						var location = Global.getBaseURL() + '#!m=MyProfile&company_id=' + LocalCacheData.getAllURLArgs()['company_id'];
						window.location = location;
					} else {
						$this.uploadResume();
						$this.saveCurrentJobVacancy();
					}
				}
			} );
		} else {
			this.registerNewUser( function( user_name, password ) {
				var recruitment_api = TTAPI.APIRecruitmentAuthentication;
				recruitment_api.Login( user_name, password, {
					onResult: function( e ) {
						$this.onLoginSuccess( e );
					}
				} );
			} );
		}
	}

	availableJobsClick() {
		window.location = Global.getBaseURL() + '#!m=PortalJobVacancy&company_id=' + LocalCacheData.getAllURLArgs().company_id;
		LocalCacheData.setAllURLArgs( {} );
	}

	appliedJobsClick() {
		var $this = this;
		window.location = Global.getBaseURL() + '#!m=MyJobApplication&company_id=' + LocalCacheData.getAllURLArgs().company_id;
		LocalCacheData.setAllURLArgs( {} );
	}

	signIN() {
		var sign_controller = new SignInController( {
			_delegate: this
		} );
	}

	onLoginSuccess( e, session_id ) {
		var result;
		var $this = this;
		if ( !session_id ) {
			result = e.getResult();
		} else {
			result = session_id;
		}
		ServiceCaller.cancel_all_error = false;
		LocalCacheData.setSessionID( result );
		setCookie( Global.getSessionIDKey(), result );
		var jobapplicant_api = TTAPI.APIJobApplicantPortal;
		jobapplicant_api.getJobApplicant( {
			onResult: function( result ) {
				if ( result.isValid() ) {
					LocalCacheData.setPortalLoginUser( result.getResult()[0] );
					IndexViewController.instance.router.headerView.render();
					$this.uploadResume();
					$this.saveCurrentJobVacancy();
				} else {
					ProgressBar.closeOverlay();
				}
			}
		} );
	}

	setRegisterContainerStatus( callback ) {
		var $this = this;
		this.getJobApplication( function( res ) {
			var result = false;
			if ( res.isValid() ) {
				var result_data = res.getResult();
				if ( !Global.isArray( result_data ) ) {
					result_data = [];
				}
				if ( result_data.length > 0 ) {
					// $this.$('.apply-form .form-group').hide();
					// $this.hideApplyForm();
					result = true;
				}
			}
			callback( result );
		} );
	}

	// hideApplyForm: function () {
	// 	this.$('.apply-form .form-group').hide();
	// 	this.$('.apply-form button[id="applyBtn"]').text( $.i18n._('Applied')  ).attr('disabled', 'disabled');
	// },

	// showApplyForm: function () {
	// 	this.$('.apply-form .form-group').show();
	// 	this.$('.apply-form button[id="applyBtn"]').html( $.i18n._('Apply') + '<span class="glyphicon glyphicon-menu-right" aria-hidden="true"></span>' ).removeAttr('disabled');
	// },

	getJobApplication( callback ) {
		var $this = this;
		var filter = {};
		filter.filter_data = {};
		filter.filter_data.job_vacancy_id = this.model.id;
		this.job_applicant_api.getJobApplication( filter, {
			onResult: function( result ) {
				callback( result );
			}
		} );
	}

	saveCurrentJobVacancy( callback ) {
		var $this = this;
		var record = {};
		record.job_vacancy_id = this.model.id;
		this.job_applicant_api.setJobApplication( record, {
			onResult: function( result ) {
				if ( !result.isValid() ) {
					$this.showValidateErrorTips( result );
				} else {
					$this.render();
				}
			}
		} );
	}

	showAlert( content ) {
		IndexViewController.instance.router.showTipModal( content, {
			style: {
				'border': '2px solid #154575',
				'color': '#154575'
			}
		} );
	}

	uploadResume() {
		if ( !this.uploader ) {
			return false;
		}
		var uploader = this.uploader;
		var name = uploader.attr( 'file_name' );
		var file = uploader.getValue();
		if ( file ) {
			this.document_api = TTAPI.APIDocumentPortal;
			var document_name = 'Resume' + ' - ' + this.$( '.apply-form input[id="first_name"]' ).val() + ' ' + this.$( '.apply-form input[id="last_name"]' ).val();
			var document_revision_id = this.document_api.addAttachment( document_name, '', { async: false } ).getResult()['document_revision_id'];
			this.uploadFile( file, document_revision_id );
		} else {
			this.applySuccess();
		}
	}

	applySuccess() {
		var url = Global.getBaseURL() + '#!m=MyProfile&company_id=' + LocalCacheData.getAllURLArgs()['company_id'];
		var content = $( '<div>' );
		content.append( '<p>' + $.i18n._( 'Application sent successfully!' ) + '</p>' );
		content.append( '<p>' + $.i18n._( 'Please ensure that your profile is accurate and complete.' ) + '</p>' );
		this.showAlert( content );
		window.location = url;
	}

	uploadFile( file_data, document_revision_id ) {
		var $this = this;
		var url = ServiceCaller.getAPIURL( 'Class=' + this.document_api.className + '&Method=uploadAttachment' + '&v=2' );
		if ( LocalCacheData.getAllURLArgs().company_id ) {
			url = url + '&company_id=' + LocalCacheData.getAllURLArgs().company_id;
		}
		url = url + '&object_id=' + document_revision_id;
		var message_id = TTUUID.generateUUID();
		ProgressBar.showProgressBar( message_id );
		ProgressBar.changeProgressBarMessage( 'File Uploading' );
		$.ajax( {
			url: url, //Server script to process data
			headers: {
				//Handle CSRF tokens and related headers here.
				'X-Client-ID': 'Browser-TimeTrex',
				'X-CSRF-Token': getCookie( 'CSRF-Token' ),
			},
			type: 'POST',
			data: file_data,
			success: function( res ) {
				ProgressBar.removeProgressBar();
				if ( res.error == '' ) {
					$this.applySuccess();
				}
			},
			cache: false,
			contentType: false,
			processData: false
		} );
	}

	registerNewUser( callBack ) {
		var $this = this;
		var data = {};
		data.first_name = this.$( '.apply-form input[id="first_name"]' ).val();
		data.last_name = this.$( '.apply-form input[id="last_name"]' ).val();
		data.user_name = this.$( '.apply-form input[id="email"]' ).val();
		data.email = data.user_name;
		data.password = Math.random().toString( 36 ).replace( /[^a-z0-9]+/g, '' ); //Use a random password for new users.
		data.company_id = LocalCacheData.getAllURLArgs().company_id;
		if ( this.$( '.apply-form input[id="email"]' ).val() === this.$( '.apply-form input[id="confirm_email"]' ).val() ) {
			if ( this.edit_view_error_ui_dic.hasOwnProperty( 'confirm_email' ) ) {
				this.edit_view_error_ui_dic['confirm_email'].removeClass( 'error-tip' );
				this.edit_view_error_ui_dic['confirm_email'].tooltip().qtip( 'destroy' );
			}
			if ( data.first_name != '' && data.last_name != '' && data.email != '' ) {
				if ( !this.uploader.getValue() ) {
					IndexViewController.instance.router.showConfirmModal( $.i18n._( 'Are you sure you want to apply for this position without uploading a resume?' ), {
						title: '',
						actions: [
							{
								label: 'No', isClose: true, callBack: function( e ) {
									return;
								}
							},
							{
								label: 'Yes', callBack: function( e ) {
									IndexViewController.instance.router.hideConfirmModal();
									$this.setJobApplicant( data, callBack );
								}
							}
						]
					} );
					return;
				}
			}
			this.setJobApplicant( data, callBack );
		} else {
			this.removeValidateErrorTips();
			var field_obj = this.$( '.apply-form' ).find( 'input[id="confirm_email"]' );
			field_obj.addClass( 'error-tip' );
			field_obj.tooltip( {
				'title': $.i18n._( 'Confirm email does not match email' )
			} );
			field_obj.tooltip( 'show' );
			this.edit_view_error_ui_dic['confirm_email'] = field_obj;
		}
	}

	setJobApplicant( data, callBack ) {
		var $this = this;
		this.job_applicant_api.setJobApplicant( data, {
			onResult: function( result ) {
				if ( result.isValid() ) {
					$this.removeValidateErrorTips();
					$this.edit_view_error_ui_dic = {};
					callBack( data.user_name, data.password );
				} else {
					$this.showValidateErrorTips( result );
				}
			}
		} );
	}

	showValidateErrorTips( result, stepsWidgetDic ) {
		var $this = this;

		var details = result.getDetails();
		if ( !details ) {
			details = result.getDescription(); // If the details is empty, try to get description to show.
		}

		var $this = this;
		if ( Global.isArray( details ) || typeof details === 'object' ) {
			this.removeValidateErrorTips();
			$.each( details, function( index, val ) {
				if ( val.hasOwnProperty( 'error' ) ) {
					val = val.error;
				}
				// var row = Global.loadWidget( 'views/portal/alert/alert.html' );
				// $this.$('.apply-form').before( row );
				for ( var key in val ) {
					if ( !val.hasOwnProperty( key ) ) {
						continue;
					}

					var error_string = '';
					if ( key === 'user_name' ) {
						// error_string = val[key][0];
						key = 'email';
						error_string = $.i18n._( 'Email address already exists, please login instead' );
					}

					var field_obj = null;
					if ( $this.$( '.apply-form' ).find( 'input[id="' + key + '"]' )[0] ) {
						field_obj = $this.$( '.apply-form' ).find( 'input[id="' + key + '"]' );
					} else {
						//If no form field is found, show error attached to the form itself.
						field_obj = $this.$( '.apply-form' );
					}

					if ( field_obj ) {
						field_obj.addClass( 'error-tip' );
						if ( !error_string ) {
							if ( _.isArray( val[key] ) ) {
								error_string = val[key][0];
							} else {
								error_string = val[key];
							}
						}
						// field_obj.tooltip({
						// 	'title': error_string,
						// })
						field_obj.attr( { 'title': error_string, 'data-original-title': error_string } );
						field_obj.tooltip( 'show' );
						$this.edit_view_error_ui_dic[key] = field_obj;
						// sort_array.push( field_obj.attr('data-sort') );
					}
				}
			} );
		}
		return false;
	}

	removeValidateErrorTips() {
		// this.$('.apply-form').find('input').css('border', '1px solid #c7e0f2');
		for ( var key in this.edit_view_error_ui_dic ) {
			if ( key === 'email' || key === 'confirm_email' ) {
				continue;
			}
			if ( this.edit_view_error_ui_dic[key].val() !== '' ) {
				this.edit_view_error_ui_dic[key].removeClass( 'error-tip' );
				this.edit_view_error_ui_dic[key].tooltip().qtip( 'destroy', true );
			}
			// this.edit_view_error_ui_dic[key].removeClass('error-tip');
			// this.edit_view_error_ui_dic[key].tooltip('destroy');
		}
		// this.edit_view_error_ui_dic = {};
		// if ( typeof this.$('.job-apply-form .alert')[0] !== 'undefined' ) {
		// 	this.$('.job-apply-form .alert').remove();
		// }
	}
}
