import { PortalBaseViewController } from '@/views/portal/PortalBaseViewController';
import { TTAPI } from '@/services/TimeTrexClientAPI';
import { PortalJobVacancyDetailController } from '@/views/portal/hr/recruitment/PortalJobVacancyDetailController';
import { IndexViewController} from '@/portal/recruitment/IndexController';

export class PortalJobVacancyDetailViewController extends PortalBaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '.job-vacancy-detail-view',
			api: null,

			// _required_files: ['PortalJobVacancyDetailController']
		} );

		super( options );
	}

	init() {
		this.script_name = 'JobVacancyDetailView';
		this.api = TTAPI.APIJobVacancyPortal;
		this.job_detail_panel = this.$( '.search-result' );
		this.job_detail_panel.empty();
		this.render();
	}

	render() {
		var $this = this;
		ProgressBar.showOverlay();
		this.search();
	}

	search() {
		var $this = this;
		var filter = {};
		filter.filter_data = {};
		if ( LocalCacheData.edit_id_for_next_open_view ) {
			this.view_id = LocalCacheData.edit_id_for_next_open_view;
			filter.filter_data.id = [this.view_id];
			ProgressBar.showOverlay();
			this.api.getJobVacancy( filter, {
				onResult: function( result ) {
					var result_data = result.getResult();
					if ( !result_data ) {
						result_data = [];
					}
					result_data = result_data[0];
					// $this.current_edit_record = result_data;
					if ( result_data ) {
						var detail_model = new Backbone.Model( result_data );
						var portal_job_vacancy_detail = new PortalJobVacancyDetailController( {
							model: detail_model,
							el: $this.$el
						} );
						IndexViewController.instance.router.headerView.jobVacancyDetailController = portal_job_vacancy_detail;
					}
				}
			} );
			LocalCacheData.edit_id_for_next_open_view = null;
		}
	}

	goPortalLogin() {
		if ( window.location.href === Global.getBaseURL() + '#!m=PortalJobVacancy' ) {
			IndexViewController.instance.router.reloadView( 'PortalJobVacancy' );
		} else {
			var location = Global.getBaseURL() + '#!m=PortalJobVacancy';
			if ( LocalCacheData.getAllURLArgs().company_id ) {
				for ( var key in LocalCacheData.getAllURLArgs() ) {
					if ( key !== 'm' ) {
						location = location + '&' + key + '=' + LocalCacheData.getAllURLArgs()[key];
					}
				}
				window.location = location;
			}
		}
	}
}